@import "shared";

#text-container {
	width: 60vw;
	height: 100vh;
	position: absolute;
	left: 0;
	top: 0;
	padding-left: 20vw;
	padding-right: 20vw;
	margin: 0;
	text-align: center;
}

@media (max-width:960px){
	#text-container {
		width: 80vw;
		padding-left: 10vw;
		padding-right: 10vw;
	}
}

@media (max-width:640px){
	#text-container {
		width: 90vw;
		padding-left: 5vw;
		padding-right: 5vw;
	}
}

#title-pic {
	width: 100%;
	margin: 0;
	margin-top: 5vh;
}

#intro-photo {
	position: fixed;
	width: 69.6vh;
	height: 90vh;
	margin: 0;
}

#photo-container {
	position: absolute;
	left: 50vw;
	padding-left: 5vw;
	padding-top: 5vh;
	top: 0;
}

.about-text {
	width: 100%;
	margin-bottom: 3vh;
	text-align: justify;
}

.caption {
	width: 100%;
	text-align: justify;
}

.caption-side {
	width: 100%;
	text-align: justify;
	margin-top: 0;
}

.by {
	font-size: 2.5vh;
	margin-bottom: .3vh;
	margin-top: 0;
}

#head {
	margin-top: 3vh;
	font-size: 5vh;
	margin-bottom: 3vh;
}

.date {
	margin-bottom: 2vh;
}

.by {
	margin-bottom: 3vh;
}

.disclaimer {
	margin-left: 2vw;
	margin-right: 2vw;
	font-size: 1.75vh;
	padding-bottom: 5vh;
}

.about-pic-container {
	width: 30vw;
	float: right;
	margin-left: 5vw;
}

.full-width-container {
	width: 100%;
	float: right;
	margin-left: 5vw;
	display: block;
}

#in-memoriam {
	width: 50%;
	float: left;
	padding-right: 2.5vw;
	margin-right: 2.5vw;
	display: block;
	margin-bottom: 0.67em;
	table-layout: fixed;
	border-right: 1px solid black;
}

#in-memoriam td {
	width: 33%;
}

#in-memoriam td img {
	border: 1px solid black;
}

#in-memoriam h3 {
	text-align: justify;
	margin-top: 0;
	margin-bottom: 10px;
}

#in-memoriam h4 {
	text-align: justify;
	margin-top: 0;
	margin-bottom: 10px;
}

#about-pic {
	width: 100%;
	align: right;
}

.in-memoriam-pic {
	width: 100%;
}

a {
	color: black;
	text-decoration: none;
}

a :hover {
	color: black;
	text-decoration: underline;
}

body {
	background-color: $default-background-color;
}

